a, a:hover {
    text-decoration: none;
}

a {
    color: inherit;

    &:hover {
        color: $primary;
    }

}

.body-link {
    text-transform: uppercase;
    font-weight: 700;
    font-family: $display-font-family;
    position: relative;

    &:before {
        content: '';
        position: absolute;
        bottom: -$spacer * .25;
        right: 0;
        width: 100%;
        height: 1px;
        background-color: $primary;
        transition: all .3s ease-in-out;
    }

    &:hover {
        color: inherit;
        &:before {
            width: 0;
        }
    }

}