@each $title, $value in $theme-colors {
    .bg-#{$title} {
        background-color: var(--#{$title}) !important;
    }
}

@each $breakpoint-title, $breakpoint-value in $grid-breakpoints {
    @each $title, $value in $spacers {
        @if $breakpoint-value == 0 {
            .py-#{$title}, .pt-#{$title} {
                padding-top: $value !important;
            }

            .py-#{$title}, .pb-#{$title} {
                padding-bottom: $value !important;
            }

            .px-#{$title}, .ps-#{$title} {
                padding-left: $value !important;
            }

            .px-#{$title}, .pe-#{$title} {
                padding-right: $value !important;
            }

            .my-#{$title}, .mt-#{$title} {
                margin-top: $value !important;
            }

            .my-#{$title}, .mb-#{$title} {
                margin-bottom: $value !important;
            }

            .mx-#{$title}, .ms-#{$title} {
                margin-left: $value !important;
            }

            .mx-#{$title}, .me-#{$title} {
                margin-right: $value !important;
            }
        } @else {
            @media screen and (min-width: $breakpoint-value) {
                .py-#{$breakpoint-title}-#{$title}, .pt-#{$breakpoint-title}-#{$title} {
                    padding-top: $value !important;
                }
    
                .py-#{$breakpoint-title}-#{$title}, .pb-#{$breakpoint-title}-#{$title} {
                    padding-bottom: $value !important;
                }
    
                .px-#{$breakpoint-title}-#{$title}, .ps-#{$breakpoint-title}-#{$title} {
                    padding-left: $value !important;
                }
    
                .px-#{$breakpoint-title}-#{$title}, .pe-#{$breakpoint-title}-#{$title} {
                    padding-right: $value !important;
                }
    
                .my-#{$breakpoint-title}-#{$title}, .mt-#{$breakpoint-title}-#{$title} {
                    margin-top: $value !important;
                }
    
                .my-#{$breakpoint-title}-#{$title}, .mb-#{$breakpoint-title}-#{$title} {
                    margin-bottom: $value !important;
                }
    
                .mx-#{$breakpoint-title}-#{$title}, .ms-#{$breakpoint-title}-#{$title} {
                    margin-left: $value !important;
                }
    
                .mx-#{$breakpoint-title}-#{$title}, .me-#{$breakpoint-title}-#{$title} {
                    margin-right: $value !important;
                }
            }
        }
    }
}

@each $value in $viewport-heights {
    .vh-#{$value} {
        min-height: #{$value}vh !important;
    }
}

svg.arrow-icon {
    .arrow, .border {
        fill: var(--logo-fill);
        opacity: .1;
    }

    .border {
        animation: arrow-border-rotation 20s infinite linear;
        transform-origin: 50%;
    }
}

@keyframes arrow-border-rotation {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(359deg)
    }
}

.img-fluid {
    max-width: 100%;
    height: auto;
}

hr {
    border: 0;
    border-top: 1px solid var(--navbar-border);
    margin: $spacer-2 0;
}

.border  {
    border: 1px solid var(--border-color);

    &-top {
        border-top: 1px solid var(--border-color);
    }

    &-right {
        border-right: 1px solid var(--border-color);
    }

    &-bottom {
        border-bottom: 1px solid var(--border-color);
    }

    &-left {
        border-left: 1px solid var(--border-color);
    }

}

.bg-light {
    --border-color: #{$dark};
}