footer {
    background-color: $dark;
    color: white;
    position: relative;

    .footer-menu-toggle {
        border: none;
        box-shadow: none;
        background-color: transparent;
        color: white;
        padding: 0 $spacer-3;
        outline: none;
        all: reset;
    }

    .footer-menu {
        position: absolute;
        padding: $spacer-2;
        background-color: $dark;
        bottom: 0%;
        right: $spacer-2;
        transition: all .4s ease-in-out;
        height: 0;
        opacity: 0;
        display: none;

        &.active {
            bottom: 100%;
            height: auto;
            opacity: 1;
            display: flex;
            flex-direction: column;
        }

    }

}