body, html {
    font-family: var(--font-base) !important;
    font-weight: normal !important;
    font-weight: 400 !important;
    color: var(--type-color-base) !important;
}

p {
    font-family: var(--font-base) !important;
    font-weight: normal !important;
    font-weight: 400 !important;
    margin-top: 0;
    margin-right: 0;
    margin-left: 0;
    margin-bottom: $spacer !important;
    color: var(--type-color-base) !important;
}

.lead {
    font-size: var(--fs-5);
}

@each $title, $value in $font-sizes {
    .fs-#{$title}, .h#{$title}, h#{$title} {
        font-size: var(--fs-#{$title}) !important;
    }
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
    font-family: var(--font-headline) !important;
    font-weight: $heading-font-weight;
    font-style: $heading-font-style !important;
    line-height: $heading-line-height;
    color: var(--heading-color);
    margin: 0;
}

h6, .h6 {
    text-transform: uppercase !important;
    font-weight: 700 !important;
    font-family: $display-font-family;
    margin-bottom: $spacer;
}

.display {
    &-1, &-2, &-3, &-4, &-5, &-6 {
        font-family: var(--font-headline) !important;
        font-weight: $display-font-weight !important;
        font-style: $display-font-style !important;
        line-height: $display-line-height;
        margin: 0;
    }
}

@each $title, $value in $display-font-sizes {
    .display-#{$title} {
        font-size: var(--dfs-#{$title}) !important;
    }
} 

.text-muted {
    opacity: $text-muted;
}

@each $title, $value in $theme-colors {
    .text-#{$title} {
        color: $value !important;
    }
}

strong, .strong {
    font-weight: 700 !important;
}

em, .em {
    font-style: italic !important;
}

@each $breakpoint-title, $breakpoint-value in $grid-breakpoints {
    @if $breakpoint-value == 0 {
        .text-right {
            text-align: right;
        }
        .text-center {
            text-align: center;
        }
        .text-left {
            text-align: left;
        }
    } @else {
        @media screen and (min-width: $breakpoint-value) {
            .text-#{$breakpoint-title}-right {
                text-align: right;
            }
            .text-#{$breakpoint-title}-center {
                text-align: center;
            }
            .text-#{$breakpoint-title}-left {
                text-align: left;
            }
        }
    }
}

.masthead {
    position: fixed;
    z-index: 100000000;
    top: 60px;
    left: 0;
    right: 0;
}

.site-title {
    text-align: center;
    font-family: $display-font-family;
    font-size: 9vw !important;
    text-transform: uppercase;
    font-weight: 700 !important;
    line-height: 1;
}

ul {

    padding-left: $spacer;

    li {
        padding-left: $spacer;
    }
    li::marker {
        content: '—';
        color: $primary;
    }
}