// Colors
$primary:               #FB4413;
$dark:                  #000012;
$light:                 #f2f2f2;
$white:                 #fff;

$theme-colors: (
    'primary': $primary,
    'dark': $dark,
    'light': $light,
    'white': $white,
) !default;

//Typography
$display-font-family:           'Sohne Breit' !important;
$headline-font-family:          'Newsreader';
$base-font-family:              'Schibsted Grotesk';
$mono-font-family:              'Sono';
$type-color-base:               $primary !important;
$type-color-dark:               $white;
$text-muted:                    .7 !important;
$font-size-base:                1rem !default;

$h1-font-size:                  $font-size-base * 2.5 !default;
$h2-font-size:                  $font-size-base * 2 !default;
$h3-font-size:                  $font-size-base * 1.75 !default;
$h4-font-size:                  $font-size-base * 1.5 !default;
$h5-font-size:                  $font-size-base * 1.25 !default;
$h6-font-size:                  $font-size-base !default;
$heading-font-weight:           400 !important;
$heading-font-style:            normal;
$heading-line-height:          1.1;
$heading-color:                var(--dark);
$heading-color-dark:           var(--white);


$font-sizes: (
  1: $h1-font-size,
  2: $h2-font-size,
  3: $h3-font-size,
  4: $h4-font-size,
  5: $h5-font-size,
  6: $h6-font-size
) !default;

$display-font-weight:           400;
$display-font-style:            normal;
$display-line-height:           1.1;

$display-font-sizes: (
  1: clamp(7vw, 5rem, 15vw),
  2: 4.5rem,
  3: 4rem,
  4: 3.5rem,
  5: 3rem,
  6: 2.5rem
) !default;

// Spacers
$spacer:                        1rem;
$spacer-2:                      $spacer * 1.250;
$spacer-3:                      $spacer-2 * 1.250;
$spacer-4:                      $spacer-3 * 1.250;
$spacer-5:                      $spacer-4 * 1.250;
$spacer-6:                      $spacer-5 * 1.250;
$spacer-7:                      $spacer-6 * 1.250;
$spacer-8:                      $spacer-7 * 1.250;
$spacer-9:                      $spacer-8 * 1.250;
$spacer-10:                     $spacer-9 * 1.250;

$spacers: (
    1: $spacer,
    2: $spacer-2,
    3: $spacer-3,
    4: $spacer-4,
    5: $spacer-5,
    6: $spacer-6,
    7: $spacer-7,
    8: $spacer-8,
    9: $spacer-9,
    10: $spacer-10,
) !default;

// Breakpoints
$breakpoints-xs: 0;
$breakpoints-sm: 576px;
$breakpoints-md: 768px;
$breakpoints-lg: 992px;
$breakpoints-xl: 1200px;
$breakpoints-xxl: 1400px;

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
) !default;

//Navigation
$logo-color:                    $primary;
$logo-color-dark:               $white;
$nav-link:                      $dark;
$nav-link-dark:                 $white;
$nav-link-hover:                $primary;
$nav-background:                $white;
$nav-background-dark:           $dark;


//Utilities
$viewport-heights: (100, 90, 80, 70, 60, 50, 40, 30, 20, 10 ) !default;