.casestudy- {
    &jumbotron {
        position: relative;
        background-color: $light;
        z-index: 0;

        .container-fluid {
            min-height: 100vh;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .image {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: -1;

            figure {
                height: 600px;
                opacity: .5;
            }

        }

    }
}

.image-section {
    .casestudy-assets {
        position: relative !important;
        border-top: 1px solid var(--border-color);
        .content {
            padding-top: $spacer-4;
            
            @media screen and (min-width: $breakpoints-md) {
                border-right: 1px solid var(--border-color);
                position: sticky;
                top: 120px;
                height: 100%;
                padding-right: $spacer;
            }

            @media screen and (max-width: $breakpoints-md) {
                position: relative;
                border-bottom: 1px solid var(--border-color);
                border-right: none;
            }

        }

        .images {
            padding-top: $spacer-4;

            img {
                display: block;
                width: 100%;
                height: auto;
                margin-bottom: calc($spacer / 2) !important;
            }

            .caption {
                margin-bottom: var(--bs-gutter-x) !important;
                opacity: .4;
                text-transform: capitalize;
                font-family: $mono-font-family !important;
                font-size: 12px;
                text-align: right;
            }

        }


    }
}