.homepage {
    position: relative;
    min-height: 100vh;
    


    .project-list {
        position: relative;

        .featured-project {
            background-color: $light;
            max-height: 100vh;
            position: sticky;
            top: 0;
            padding-top: $spacer;
            padding-bottom: $spacer-6;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;

            @media screen and (max-width: $breakpoints-md) {
                position: relative;
                background-color: white;
                padding-top: $spacer-8;
            }

            figure {
                width: 100%;
                height: 100%;
                max-height: 400px;
                max-width: 700px;
                overflow: hidden;
                margin: 0 auto;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    object-position: center; 
                }

            }

        }

    }

}