:root {
    @each $title, $value in $theme-colors {
        --#{$title}: #{$value} !important;
    }
    @each $title, $value in $theme-colors {
        --type-#{$title}: #{$value} !important;
    }
    --type-color-base: #{$type-color-base} !important;
    --heading-color:   #{$heading-color} !important;
    @each $title, $value in $font-sizes {
        --fs-#{$title}: #{$value} !important;
    }
    @each $title, $value in $display-font-sizes {
        --dfs-#{$title}: #{$value} !important;
    }
    --font-headline: '#{$headline-font-family}' !important;
    --font-base: '#{$base-font-family}' !important;
    --font-mono: '#{$mono-font-family}' !important;
    @each $title, $value in $spacers {
        --spacer-#{$title}: #{$value};
    }
    --nav-link: #{$nav-link} !important;
    --nav-bg: #{$nav-background} !important;
    --border-color: #{$light} !important;
    --bs-gutter-x: 1.5rem;
}